.header {
  width: 100%;
  position: fixed;
  padding-top: 16px;
  background-color: #fff;
  z-index: 1;
  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__box {
    display: flex;
    gap: 14px;
  }
}

.phone {
  background-color: transparent;
  border: none;
}

.hamburger {
  background-color: transparent;
  border: none;
}
